@import '../../../../node_modules/swiper/css/swiper.min.css';

.swiper-title {
  font: 800 33px 'Roboto',sans-serif; }

.swiper-container-new {
  width: 50%;
  padding-top: 50px;
  padding-bottom: 66px;
  overflow: hidden;
  margin: 0 auto 20px; }

.swiper-slide {
  background-position: center;
  background-size: cover;
  width: 300px;
  height: 300px;

  a {
    position: absolute;
    height: 100%;

    span {
      height: 20%;
      width: 118%;
      background-color: rgba(10,10,10,.5);
      color: #fff;
      display: flex;
      align-items: center;
      padding: 10px 11px;
      position: absolute;
      bottom: 0; } } }
